import styled from "styled-components";

export const InfoContainer = styled.nav`
  color: #fff;
  height: 1100px;
  background: ${({ lightBg }) => (lightBg ? "#191919" : "#191919")};
  padding: 0 30px;
  @media screen and (max-width: 768px) {
    padding: 100px 0;
  }
  @media screen and (max-width: 480px) {
    height: 800px;
  }
`;

export const AbsoluteLeft = styled.p`
  color: #fff;
  padding-top: 80px;
  left: 30px;
  font-size: 50px;
  font-weight: bold;
  width: 50%;

  @media screen and (max-width: 768px) {
    color: #fff;
    top: 10px;
    z-index: 2;
    left: 0;
    font-size: 25px;
    width: 50%;
  }
  @media screen and (max-width: 480px) {
    color: #fff;
    z-index: 2;
    margin-top: -100px;
    padding-left: 20px;
    font-size: 25x;
    width: 50%;
  }
`;

export const AbsoluteLeft2 = styled.p`
  color: #fff;
  top: 700px;
  left: 30px;
  font-size: 60px;
  font-weight: bold;
  width: 820px;

  @media screen and (max-width: 768px) {
    position: absolute;
    color: #fff;
    top: 105px;
    z-index: 2;
    left: 0;
    font-size: 25px;
    width: 50%;
  }
  @media screen and (max-width: 480px) {
    position: absolute;
    color: #fff;
    top: 110px;
    z-index: 2;
    left: 20px;
    font-size: 25x;
    width: 50%;
  }
`;
export const InfoWrapper = styled.nav`
  display: grid;
  z-index: 1;
  height: 860px;
  width: 100%;
  max-width: 1100px;
  margin-right: auto;
  margin-left: auto;
  padding: 0 24px;
  justify-content: center;
`;

export const InfoRow = styled.div`
  display: grid;
  grid-auto-columns: minmax(auto, 1fr);
  align-items: center;
  grid-template-areas: ${({ imgStart }) =>
    imgStart ? `'col2 col1'` : `'col1 col2'`};
  @media screen and (max-width: 768px) {
    padding: 100px 0;
    grid-template-areas: ${({ imgStart }) =>
      imgStart ? `'col1' 'col2'` : `'col1 col1' 'col2 col2'`};
  }
`;

export const Column1 = styled.div`
  margin-bottom: 15px;
  padding: 0 15px;
  grid-area: col1;
`;

export const Column2 = styled.div`
  margin-bottom: 15px;
  padding: 0 15px;
  grid-area: col2;
`;

export const TextWrapper = styled.div`
  max-width: 540px;
  padding-top: 0;
  padding-bottom: 60px;
`;

export const TopLine = styled.p`
  color: #01bf71;
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  letter-spacing: 1.4px;
  text-transform: uppercase;
  margin-bottom: 16px;
`;

export const Heading = styled.h1`
  margin-bottom: 24px;
  font-size: 48px;
  line-height: 1.1;
  font-weight: 600;
  color: ${({ lightText }) => (lightText ? "#f7f8fa" : "#010606")};

  @media screen and (max-width: 480px) {
    font-size: 32px;
  }
`;

export const SubTitle = styled.p`
  max-width: 440px;
  margin-bottom: 35px;
  font-size: 18px;
  line-height: 24px;
  color: ${({ darkText }) => (darkText ? "#010606" : "#fff")};
`;

export const BtnWrap = styled.div`
  display: flex;
  justify-content: flex-start;
`;

export const ImgWrap = styled.div`
  max-width: 555px;
  height: 100%;
`;

export const Img = styled.img`
  width: 100%;
  margin: 0 0 10px 0;
  padding-right: 0;
`;
