import React from "react";

const ProgressBar = ({
  bgcolor,
  progress,
  height,
  marginTop,
  marginLeft,
  marginRight,
  marginBottom,
}) => {
  const Parentdiv = {
    height: height,
    width: "100%",
    backgroundColor: "#646464",
    borderRadius: 40,
    marginTop: marginTop,
    marginLeft: marginLeft,
    marginRight: marginRight,
    marginBottom: marginBottom,
  };

  const Childdiv = {
    height: "100%",
    width: `${progress}%`,
    backgroundColor: bgcolor,
    borderRadius: 40,
  };

  return (
    <div style={Parentdiv}>
      <div style={Childdiv}></div>
    </div>
  );
};

export default ProgressBar;
