import React, { useState, useEffect } from "react";
import { Modal } from "react-responsive-modal";

import { FaBars } from "react-icons/fa";
import { IconContext } from "react-icons/lib";
import { animateScroll as scroll } from "react-scroll";
import SvgMPlace from "../../assets/svgMPlace.svg";
import {
  Nav,
  NavbarContainer,
  NavLogo,
  MobileIcon,
  NavMenu,
  NavItem,
  NavLinks,
  NavBtn,
  NavBtnLink,
  NavMenuLeft,
  NavMenuRight,
  LogoImage,
} from "./NavbarElements";

const Navbar = ({ toggle }) => {
  const [scrollNav, seTscrollNav] = useState(false);

  const changeNav = () => {
    if (window.scrollY >= 80) {
      seTscrollNav(true);
    } else {
      seTscrollNav(false);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", changeNav);
  }, []);

  const toggleHome = () => {
    scroll.scrollToTop();
  };

  const [open, seTopen] = useState(false);

  const onOpenModal = () => seTopen(true);
  const onCloseModal = () => seTopen(false);

  return (
    <>
      <IconContext.Provider value={{ color: "#b9fd02" }}>
        <Nav scrollNav={scrollNav}>
          <NavbarContainer>
            <NavMenuLeft>
              <NavItem>
                <NavLinks
                  to="about"
                  smooth={true}
                  duration={500}
                  spy={true}
                  exact="true"
                  offset={0}
                >
                  ABOUT PROJECT
                </NavLinks>
              </NavItem>
              <NavItem>
                <NavLinks
                  to="marketplace"
                  smooth={true}
                  duration={500}
                  spy={true}
                  exact="true"
                  offset={0}
                >
                  <img
                    style={{ marginRight: "5px" }}
                    src={SvgMPlace}
                    alt="React Logo"
                  />
                  MARKETPLACE
                </NavLinks>
              </NavItem>
              <NavItem>
                <NavLinks
                  to="teams"
                  smooth={true}
                  duration={500}
                  spy={true}
                  exact="true"
                  offset={0}
                >
                  TEAMS
                </NavLinks>
              </NavItem>
            </NavMenuLeft>
            <MobileIcon onClick={toggle}>
              <FaBars />
            </MobileIcon>

            <NavLogo to="/" onClick={toggleHome}>
              <LogoImage src={require("../../assets/Logo.svg").default} />
            </NavLogo>
            <NavMenuRight>
              <NavItem>
                <NavLinks
                  to="faq"
                  smooth={true}
                  duration={500}
                  spy={true}
                  exact="true"
                  offset={0}
                >
                  FAQ
                </NavLinks>
              </NavItem>
              <NavBtn>
                <NavBtnLink onClick={onOpenModal}>SUBSCRIBE</NavBtnLink>
              </NavBtn>
            </NavMenuRight>
          </NavbarContainer>
        </Nav>
      </IconContext.Provider>
      <Modal open={open} onClose={onCloseModal} center>
        <h2>modal</h2>
        <p>
          pulvinar risus non risus hendrerit venenatis. Pellentesque sit amet
          hendrerit risus, sed porttitor quam.
        </p>
      </Modal>
    </>
  );
};

export default Navbar;
