import styled from "styled-components";
import { Link as LinkR } from "react-router-dom";

export const InfoContainer = styled.nav`
  color: #fff;
  height: 1650px;
  padding: 0 30px;
  background: #191919;

  z-index: 1;
  width: 100%;
  /* Add :before styles */
  :before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0.2) 0%,
      rgba(0, 0, 0, 0.6) 100%,
      linear-gradient(180deg, rgba(0, 0, 0, 0.2) 0%, transparent 100%)
    );
    z-index: 2;
  }
  @media screen and (max-width: 768px) {
  }
  @media screen and (max-width: 480px) {
    height: 0px;
  }
`;

export const AboutContent = styled.nav`
  top: 1000px;
  z-index: 3;
  width: 100%;
  height: 50%;
  position: absolute;
  padding: 8px 24px;
  display: flex;
  flex-direction: row;
  align-items: center;
`;

export const AbsoluteLeft = styled.p`
  position: absolute;
  color: #fff;
  top: 600px;
  left: 30px;
  font-size: 60px;
  font-weight: bold;
  width: 820px;

  @media screen and (max-width: 768px) {
    position: absolute;
    color: #fff;
    top: 105px;
    z-index: 2;
    left: 0;
    font-size: 25px;
    width: 50%;
  }
  @media screen and (max-width: 480px) {
    position: absolute;
    color: #fff;
    top: 110px;
    z-index: 2;
    left: 20px;
    font-size: 25x;
    width: 50%;
  }
`;

export const AbsoluteLeft2 = styled.p`
  position: absolute;
  color: #fff;
  top: 700px;
  left: 30px;
  font-size: 60px;
  font-weight: bold;
  width: 820px;

  @media screen and (max-width: 768px) {
    position: absolute;
    color: #fff;
    top: 105px;
    z-index: 2;
    left: 0;
    font-size: 25px;
    width: 50%;
  }
  @media screen and (max-width: 480px) {
    position: absolute;
    color: #fff;
    top: 110px;
    z-index: 2;
    left: 20px;
    font-size: 25x;
    width: 50%;
  }
`;
export const AbsoluteRight = styled.div`
  position: absolute;
  text-transform: uppercase;
  top: 250px;
  color: #f5f5f0;
  right: 40px;
  font-weight: bold;
  font-size: 56px;
  width: 68%;
  z-index: 1;
  letter-spacing: 2.33333px;

  @media screen and (max-width: 768px) {
    position: absolute;
    color: #f5f5f0;
    top: 150px;
    z-index: 2;
    font-size: 30px;
  }
  @media screen and (max-width: 480px) {
    position: absolute;
    color: #f5f5f0;
    left: 20px;
    top: 230px;
    z-index: 2;
    font-size: 12px;
    width: 220px;
  }
`;

export const AbsoluteRightSecond = styled.div`
  position: absolute;
  top: 450px;
  color: #f5f5f0;
  right: 40px;
  font-weight: bold;
  font-size: 45px;
  width: 60%;
  z-index: 1;
  letter-spacing: 2.33333px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  background-color: orange;

  @media screen and (max-width: 768px) {
    position: absolute;
    color: #f5f5f0;
    top: 150px;
    z-index: 2;
    font-size: 30px;
  }
  @media screen and (max-width: 480px) {
    position: absolute;
    color: #f5f5f0;
    left: 20px;
    top: 230px;
    z-index: 2;
    font-size: 12px;
    width: 220px;
  }
`;

export const Lenear = styled.div`
  position: relative;
  top: 100px;
  width: 96%;
  border-width: 1px;
  border-radius: 10px;
  background: linear-gradient(#87289d, #396afa);
  borderradius: 12px;
  color: #f5f5f0;
  padding-top: 20px;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 30px;

  @media screen and (max-width: 768px) {
  }
  @media screen and (max-width: 480px) {
    width: 90%;
    margin-top: -130px;
    height: 30%;
    left: 20px;
    flex-direction: column;
  }
`;

export const Input = styled.input`
  padding: 0.5em;
  margin: 0.5em;
  color: ${(props) => props.inputColor || "#000"};
  background: transparent;
  border: none;
  width: 400px;
`;

export const NavBtn = styled.nav`
  display: flex;
  align-items: center;
  margin-left: 8px;

  @media screen and (max-width: 768px) {
    display: none;
  }
`;
export const NavBtnLink = styled(LinkR)`
  border-radius: 8px;
  background: #b9fd02;
  white-space: nowrap;
  padding: 10px 30px;
  color: #010606;
  font-size: 16px;
  outling: none;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  text-decoration: none;
  width: 400px;
  text-align: center;
  &:hover {
    transition: all 0.2 ease-in-out;
    background: #fff;
    color: #010606;
  }
`;

export const LenearFlexLeft = styled.div`
  width: 30%;
  right: 0;
  font-size: 30px;
  margin-left: 15px;
  font-weight: bold;
  background-color: red;

  @media screen and (max-width: 768px) {
  }
  @media screen and (max-width: 480px) {
    margin-top: 0px;
    margin-left: 15px;
    font-size: 15px;
  }
`;

export const LenearFlexRight = styled.div`
  border-left: solid 1px white;
  width: 51%;

  @media screen and (max-width: 768px) {
  }
  @media screen and (max-width: 480px) {
    border-top: solid 1px white;
    width: 51%;
  }
`;
export const Header = styled.nav`
  width: 70%;
  right: 0px;
  background-color: blue;
`;

export const InfoRow = styled.div`
  display: grid;
  grid-auto-columns: minmax(auto, 1fr);
  align-items: center;
  grid-template-areas: ${({ imgStart }) =>
    imgStart ? `'col2 col1'` : `'col1 col2'`};
  @media screen and (max-width: 768px) {
    padding: 100px 0;
    grid-template-areas: ${({ imgStart }) =>
      imgStart ? `'col1' 'col2'` : `'col1 col1' 'col2 col2'`};
  }
`;

export const Column1 = styled.div`
  margin-bottom: 15px;
  padding: 0 15px;
  grid-area: col1;
`;

export const Column2 = styled.div`
  margin-bottom: 15px;
  padding: 0 15px;
  grid-area: col2;
`;

export const TextWrapper = styled.div`
  max-width: 540px;
  padding-top: 0;
  padding-bottom: 60px;
`;

export const TopLine = styled.p`
  color: #01bf71;
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  letter-spacing: 1.4px;
  text-transform: uppercase;
  margin-bottom: 16px;
`;

export const Heading = styled.h1`
  margin-bottom: 24px;
  font-size: 48px;
  line-height: 1.1;
  font-weight: 600;
  color: ${({ lightText }) => (lightText ? "#f7f8fa" : "#010606")};

  @media screen and (max-width: 480px) {
    font-size: 32px;
  }
`;

export const SubTitle = styled.p`
  max-width: 440px;
  margin-bottom: 35px;
  font-size: 18px;
  line-height: 24px;
  color: ${({ darkText }) => (darkText ? "#010606" : "#fff")};
`;

export const BtnWrap = styled.div`
  display: flex;
  justify-content: flex-start;
`;

export const ImgWrap = styled.div`
  max-width: 555px;
  height: 100%;
`;

export const Img = styled.img`
  margin: 0 0 10px 0;
  padding-right: 0;
`;
